import thesaurus from './Thesaurus.json'


/*
    
*/
class ColorInfo {
    /*
        @param {String} color rgb16
        @param {String} word 
        @param {String} weight 
    */
    constructor(color, word, weight) {
        if (arguments.length <3) {
            this.color = color;
            this.word = word;
            this.weight = 1;
        }
        else {
            this.color = color;
            this.word = word;
            this.weight = weight;
        }
    }

    weightUp() {
        this.weight += 1;
    }
}



class WordsHandler {

    getThis() {
        return this;
    }

    /* 
        @function colorAnalysis
        
    */

    /*
        @function funcAnalysis
        
        @param {Array} words 
    */
    funcAnalysis(words) {
        let res = {
            "layout": [],
            "navigate": [],
            "template": []
        };
        for (let word of words) {
            if (thesaurus["Layout"][word]) {     /*  */
                res["layout"].push(thesaurus["Layout"][word]);
            }
            if (thesaurus["Navigate"][word]) {
                res["navigate"].push(thesaurus["Navigate"][word]);
            }
            if (thesaurus["Template"][word]) {
                res["template"].push(thesaurus["Template"][word]);
            }
        }

        return res;
    }

    /*
        @function findColor
        rgb
        @param {Array} word 
    */
    findColor(word) {
        if (thesaurus["Color"][word]) return thesaurus["Color"][word];
        return null;
    }

    /*
        @function Analysis
          
        @param {Array} words List
        @param {function} func  0-> 1->
    */
    Analysis(words, func) {
        let res = undefined;
        switch (func) {
            case 0:
                res = this.funcAnalysis(words);
                return res;
            case 1:
                res = this.findColor(words);
                return res;
            default:
                break;
        }
    }


}

export default WordsHandler;
export { ColorInfo };